/* RESETS */
*,
*::before,
*::after {
    box-sizing: border-box;
}

*:focus {
    outline: 3px dashed #228bec;
    outline-offset: 0;
}

html {
    font: 62.5% / 1.15 sans-serif;
}

h1,
h2 {
    margin-bottom: 0;
}

ul {
    list-style: none;
    padding: 0;
}

button {
    border: none;
    margin: 0;
    padding: 0;
    width: auto;
    overflow: visible;
    background: transparent;
    color: inherit;
    font: inherit;
    line-height: normal;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
    -webkit-appearance: none;
}

button::-moz-focus-inner {
    border: 0;
}

button,
input,
optgroup,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}

button,
input {
    overflow: visible;
}

input[type="text"] {
    border-radius: 0;
}

body {
    width: 100%;
    max-width: 68rem;
    margin: 0 auto;
    font: 1.6rem/1.25 Arial, sans-serif;
    background-color: #f5f5f5;
    color: #4d4d4d;
}

@media screen and (min-width: 620px) {
    body {
        font-size: 1.9rem;
        line-height: 1.31579;
    }
}

/* END RESETS */
.row {
    display: flex;
    flex-direction: row;
}

.col {
    display: flex;
    flex-direction: column;
}

.stack-large > * + * {
    margin-top: 2.5rem;
}

.stack-small > * + * {
    margin-top: 1.25rem;
}

.btn {
    text-transform: capitalize;
    cursor: pointer;
    background-color: #4d4d4d;
    color: #f0f0f0;
    padding-left: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
    border-radius: 5px;
    text-align: left;
}

.btn__danger {
    color: #fff;
    background-color: #ca3c3c;
    border-color: #bd2130;
    width: 75px;
    text-align: center;
    padding-left: 0;
}

.full {
    width: 80%;
}

.filesTiers > * {
    width: 50%;
}

.tier {
    background-color: #f0f0f0;
    border-radius: 10px;
    padding: 10px 25px;
    margin: 10px 0;
}

.tierForm {
    display: grid;
    grid-template-columns: 100px minmax(0, 1fr);
}
